@use "global/var" as v;
@use "global/mixins" as m;
@use "sass:color";

.header {
  background-image: linear-gradient(
      90deg,
      rgba(0, 0, 0, 0.75),
      hsla(0, 0%, 5%, 0.6)
    ),
    url(../../Imagenes/BgHeader.jpg);
  background-size: cover;
  color: v.$SmokeWhite;
}

.header-top {
  display: flex;
  align-items: center;
  flex-direction: column;
  @include m.desktop {
    flex-direction: row;
    justify-content: space-between;
    padding: 2rem 0;
  }

  .nav .menu li a {
    color: v.$SmokeWhite;
  }
}

.nav {
  .menu {
    // menu
    list-style: none;
    li {
      a {
        &:hover {
          color: color.scale(v.$SmokeWhite, $lightness: -20%);
        }
      }
    }

    // position
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
    @include m.desktop {
      flex-direction: row;
      gap: 4rem;
    }
  }
}

.dropdown-menu {
  list-style: none;
}
.dropdown-menu__item {
  display: none;
}
.header_Container {
  display: flex;

  flex-direction: column;
  justify-content: space-between;
  min-height: 70rem;
  padding: 5rem;
}
