html {
  font-size: 62.5%;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}

body {
  font-size: 1.6rem;
  line-height: 1.5;
  background-color: #f5f5f5;
  font-family: "Roboto", sans-serif;
}

.content {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

h1 {
  font-size: 4rem;
  font-weight: 700;
  color: #1c1c1c;
}

h2 {
  font-size: 3.5rem;
}

h2 {
  font-size: 3.5rem;
}

h3 {
  font-size: 2.9rem;
}

h4 {
  font-size: 2.5rem;
}

h1,
h2,
h3,
h4 {
  margin: 0;
}

.Logo {
  font-weight: 400;
  color: #f5f5f5;
}
.Logo .LogoSpan {
  font-weight: bold;
  color: #d4af37;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.resaltar {
  font-weight: bold;
}

.Boton-1 {
  background-color: #0b2c51;
  padding: 1rem;
  color: #f5f5f5;
  font-weight: bold;
  display: block;
  margin-bottom: 1rem;
  text-align: center;
  display: inline;
}
.Boton-1:hover {
  background-color: rgb(8.8, 35.2, 64.8);
  cursor: pointer;
}

.header {
  background-image: linear-gradient(90deg, rgba(0, 0, 0, 0.75), hsla(0, 0%, 5%, 0.6)), url(../../Imagenes/BgHeader.jpg);
  background-size: cover;
  color: #f5f5f5;
}

.header-top {
  display: flex;
  align-items: center;
  flex-direction: column;
}
@media (min-width: 992px) {
  .header-top {
    flex-direction: row;
    justify-content: space-between;
    padding: 2rem 0;
  }
}
.header-top .nav .menu li a {
  color: #f5f5f5;
}

.nav .menu {
  list-style: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
}
.nav .menu li a:hover {
  color: #c4c4c4;
}
@media (min-width: 992px) {
  .nav .menu {
    flex-direction: row;
    gap: 4rem;
  }
}

.dropdown-menu {
  list-style: none;
}

.dropdown-menu__item {
  display: none;
}

.header_Container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 70rem;
  padding: 5rem;
}

.Nosotros h2 {
  text-align: center;
}
.Nosotros .Nosotros__Informcion {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
}
@media (min-width: 992px) {
  .Nosotros .Nosotros__Informcion {
    flex-direction: row;
  }
}
.Nosotros .Nosotros__Informcion .Nosotros_ContenedorInfo {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 4rem;
  gap: 2rem;
}
.Nosotros .Nosotros__Informcion .Nosotros_ContenedorInfo h3 {
  text-align: center;
}
.Nosotros .Nosotros__Informcion .Nosotros_ContenedorInfo img {
  width: 10rem;
  filter: invert(100%);
}

.contacto {
  margin-top: 4rem;
  background-image: linear-gradient(90deg, rgba(0, 0, 0, 0.75), hsla(0, 0%, 5%, 0.6)), url(../../Imagenes/contacto.jpeg);
  background-size: cover;
  background-position: center;
  padding: 10rem 1rem;
}

.contacto-texto {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #f5f5f5;
  gap: 2rem;
}
.contacto-texto .Boton-1 {
  width: 15rem;
}

.swiper {
  width: 100%;
  height: 100%; /* Ajusta según necesidad */
}

.swiper-slide {
  background: #f4f4f4;
}
.swiper-slide img {
  width: 20rem;
  height: 20rem;
}
.swiper-slide .swiper-slide {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.Favoritos h2 {
  text-align: center;
  margin-bottom: 4rem;
  font-family: "Tangerine", cursive;
  font-size: 4rem;
}
.Favoritos .Favorito {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.Favoritos .Favorito .nombre {
  font-family: "Tangerine", cursive;
  font-size: 3rem;
  text-align: center;
}
.Favoritos .Favorito .precios {
  font-size: 1.6rem;
}
.Favoritos .Favorito .precios .precio-normal {
  text-decoration: line-through;
  opacity: 0.5;
}

.visual__content {
  position: relative;
}
.visual__content .descuento {
  position: absolute;
  background-color: #0b2c51;
  color: #f5f5f5;
  padding: 1rem;
  opacity: 0.8;
}

.publicidad {
  margin-top: 10rem;
}
.publicidad h2 {
  text-align: center;
  margin-bottom: 2rem;
  font-family: "Tangerine", cursive;
  font-size: 4rem;
}
.publicidad .anuncios {
  display: flex;
  flex-direction: column;
}
@media (min-width: 992px) {
  .publicidad .anuncios {
    flex-direction: row;
    justify-content: space-between;
  }
}
.publicidad a {
  margin-bottom: 4rem;
}
.publicidad a img {
  height: 40rem;
  width: 1545px;
}
@media (min-width: 992px) {
  .publicidad a img {
    object-fit: contain;
    max-height: 60rem;
  }
}

.footer {
  background-color: #0b2c51;
  margin-top: 10rem;
  padding: 4rem;
}
.footer .footer__container {
  color: #f5f5f5;
}
.footer .footer__container h3 {
  text-transform: uppercase;
  font-size: 2rem;
}
.footer .footer__container p {
  font-weight: 200;
}
@media (min-width: 992px) {
  .footer {
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
    display: grid;
  }
}

.footer__horarios .footer__horarios__info .footer__horarios__info__whatsapp {
  display: flex;
  align-items: center;
  gap: 1rem;
}
.footer__horarios .footer__horarios__info .footer__horarios__info__whatsapp img {
  width: 3rem;
}

.footer_informacion .footer__informacion__lista {
  font-weight: 200;
  list-style: none;
  margin-top: 1rem;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.footer__envios .footer__envios__info {
  display: flex;
  align-items: center;
  gap: 1rem;
}
.footer__envios .footer__envios__info img {
  filter: invert(100%);
  width: 6rem;
}

.footer__redes .footer__redes__info {
  margin-top: 1rem;
  display: flex;
  flex-direction: column;
}
.footer__redes .footer__redes__info .footer__red {
  display: flex;
  align-items: center;
  gap: 1rem;
}
.footer__redes .footer__redes__info .footer__red img {
  width: 3rem;
}

.footer__metodospago {
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
  display: grid;
}
.footer__metodospago img {
  width: 12rem;
}

.derechos__footer {
  color: #f5f5f5;
  text-align: center;
  margin-top: 2rem;
}

.footer__box {
  margin-bottom: 2rem;
}